"use client";

import Link from "next/link";
import { useRef } from "react";
import { useGSAP } from "@gsap/react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { getLandingImage } from "@/utils/landingImages";

gsap.registerPlugin(ScrollTrigger);

const FEATURE_ITEMS = [
  {
    title: "실 없는 바디 리프팅",
    description: "기존 실리프팅과 달리 실을 사용하지 않고 고주파와 초음파 에너지로 근막층을 자극하여 자연스러운 리프팅 효과를 구현합니다.",
  },
  {
    title: "윤곽 개선",
    description: "팔뚝, 허벅지, 복부 등 원하는 부위의 윤곽을 개선하여 더욱 탄력 있고 아름다운 바디 라인을 만들어줍니다.",
  },
  {
    title: "지방 분해",
    description: "선택적 지방 분해 기술로 원하는 부위의 지방을 효과적으로 감소시켜 자연스러운 바디 컨투어를 완성합니다.",
  },
  {
    title: "통증 최소화",
    description: "비침습적 시술로 통증과 회복 기간을 최소화하여 일상생활에 지장 없이 시술받을 수 있습니다.",
  },
];

const PROCESS_STEPS = [
  {
    step: "01",
    title: "컨설팅",
    description: "개인의 바디 상태와 목표를 분석하여 맞춤형 시술 계획을 수립합니다.",
  },
  {
    step: "02",
    title: "시술 준비",
    description: "시술 부위를 정확히 마킹하고 안전한 환경에서 시술을 진행합니다.",
  },
  {
    step: "03",
    title: "에너지 전달",
    description: "고주파와 초음파 에너지를 정확한 깊이로 전달하여 근막층을 자극합니다.",
  },
  {
    step: "04",
    title: "관리",
    description: "시술 후 관리 방법을 안내하고 정기적인 모니터링을 통해 최적의 결과를 도출합니다.",
  },
];

export default function LinkFreeBodyView() {
  const heroRef = useRef<HTMLElement>(null);
  const introRef = useRef<HTMLElement>(null);
  const featureRef = useRef<HTMLElement>(null);
  const processRef = useRef<HTMLElement>(null);

  useGSAP(
    () => {
      if (!introRef.current) return;
      gsap.fromTo(
        introRef.current,
        { opacity: 0, y: 40 },
        {
          opacity: 1,
          y: 0,
          duration: 0.9,
          ease: "power3.out",
          scrollTrigger: {
            trigger: introRef.current,
            start: "top 82%",
            toggleActions: "play none none reverse",
          },
        }
      );
    },
    { dependencies: [] }
  );

  useGSAP(
    () => {
      const cards = featureRef.current?.querySelectorAll(".feature-card");
      cards?.forEach((el, i) => {
        gsap.fromTo(
          el,
          { opacity: 0, y: 50 },
          {
            opacity: 1,
            y: 0,
            duration: 0.6,
            delay: i * 0.1,
            ease: "power3.out",
            scrollTrigger: {
              trigger: featureRef.current,
              start: "top 70%",
              toggleActions: "play none none reverse",
            },
          }
        );
      });
    },
    { scope: featureRef, dependencies: [] }
  );

  useGSAP(
    () => {
      const steps = processRef.current?.querySelectorAll(".process-step");
      steps?.forEach((el, i) => {
        gsap.fromTo(
          el,
          { opacity: 0, x: -30 },
          {
            opacity: 1,
            x: 0,
            duration: 0.7,
            delay: i * 0.15,
            ease: "power3.out",
            scrollTrigger: {
              trigger: processRef.current,
              start: "top 75%",
              toggleActions: "play none none reverse",
            },
          }
        );
      });
    },
    { scope: processRef, dependencies: [] }
  );

  return (
    <main className="min-h-screen pt-[var(--header-height)] lg:pt-[var(--header-h)]">
      {/* Hero */}
      <section
        ref={heroRef}
        className="relative min-h-[90vh] flex items-center justify-center overflow-hidden bg-gradient-to-br from-[#1a1a1a] via-[#2a2a2a] to-[#1a1a1a]"
      >
        <div
          className="absolute inset-0 bg-cover bg-center opacity-30"
          style={{
            backgroundImage: "url(https://images.unsplash.com/photo-1507003211169-0a1dd7228f2d?q=80&w=2560&auto=format&fit=crop)",
          }}
        />
        <div className="relative z-10 text-center px-6 max-w-4xl mx-auto">
          <h1 className="font-[family-name:var(--font-en-serif)] text-[clamp(48px,8vw,96px)] font-light tracking-wide text-white mb-6">
            LINK-FREE BODY
          </h1>
          <p className="text-[clamp(18px,2.5vw,24px)] text-white/90 leading-relaxed">
            실 없이 바디 라인을 개선하는<br />
            혁신적인 바디 컨투어링
          </p>
        </div>
      </section>

      {/* Intro */}
      <section
        ref={introRef}
        className="relative border-t border-[#e8e8e8] bg-white px-6 py-16 lg:px-[var(--pad-global)] lg:py-24"
      >
        <div className="mx-auto max-w-[720px] text-center">
          <h2 className="font-[family-name:var(--font-en-serif)] text-[clamp(20px,2.5vw,26px)] tracking-[0.06em] text-[#111]">
            Link-Free Body
          </h2>
          <p className="mt-4 text-[15px] leading-relaxed text-[#666]">
            LINK-FREE BODY는 기존 실리프팅과 달리 실을 사용하지 않고 고주파와 초음파 에너지를 이용하여
            근막층을 자극하고 지방을 분해하는 혁신적인 바디 컨투어링 시술입니다.
            팔뚝, 허벅지, 복부 등 원하는 부위의 윤곽을 개선하여 더욱 탄력 있고 아름다운 바디 라인을 만들어줍니다.
          </p>
        </div>
      </section>

      {/* Features */}
      <section
        ref={featureRef}
        className="hip-grain relative bg-[var(--hip-bg)] px-6 py-20 lg:px-[var(--pad-global)] lg:py-28"
      >
        <div className="mx-auto max-w-[1200px]">
          <h2 className="font-[family-name:var(--font-en-serif)] text-center text-[clamp(32px,4vw,48px)] tracking-wide text-[#111] mb-16">
            Features
          </h2>
          <div className="grid md:grid-cols-2 gap-8">
            {FEATURE_ITEMS.map((item, idx) => (
              <div
                key={idx}
                className="feature-card bg-white p-8 rounded-lg shadow-sm border border-[#e8e8e8]"
              >
                <h3 className="text-xl font-medium text-[#111] mb-3">{item.title}</h3>
                <p className="text-[15px] leading-relaxed text-[#666]">{item.description}</p>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Process */}
      <section
        ref={processRef}
        className="relative bg-white px-6 py-20 lg:px-[var(--pad-global)] lg:py-28"
      >
        <div className="mx-auto max-w-[1000px]">
          <h2 className="font-[family-name:var(--font-en-serif)] text-center text-[clamp(32px,4vw,48px)] tracking-wide text-[#111] mb-16">
            Process
          </h2>
          <div className="space-y-12">
            {PROCESS_STEPS.map((step, idx) => (
              <div key={idx} className="process-step flex gap-8 items-start">
                <div className="flex-shrink-0">
                  <span className="text-[clamp(40px,5vw,60px)] font-light text-[var(--hip-accent)]">
                    {step.step}
                  </span>
                </div>
                <div className="flex-1 pt-2">
                  <h3 className="text-2xl font-medium text-[#111] mb-3">{step.title}</h3>
                  <p className="text-[15px] leading-relaxed text-[#666]">{step.description}</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* CTA */}
      <section className="relative bg-[#1a1a1a] px-6 py-20 lg:px-[var(--pad-global)] lg:py-28">
        <div className="mx-auto max-w-[600px] text-center">
          <h2 className="font-[family-name:var(--font-en-serif)] text-[clamp(32px,4vw,48px)] text-white mb-6">
            상담 및 예약
          </h2>
          <p className="text-white/80 mb-8 leading-relaxed">
            LINK-FREE BODY 시술에 대한 자세한 상담을 원하시면<br />
            전문 상담사가 도와드리겠습니다.
          </p>
          <Link
            href="/community/contact"
            className="inline-block px-8 py-4 bg-white text-[#111] rounded-sm hover:bg-[var(--hip-accent)] hover:text-white transition-colors"
          >
            상담 예약하기
          </Link>
        </div>
      </section>
    </main>
  );
}
